              <img class="org-logo" src="@/assets/nsk_gerb.png" alt="" />
              <div class="support__owner__name">
                Департамент промышленности, инноваций и предпринимательства
              </div>
            </div>
          </div>
          <div class="support__conditions col-12 col-md-6 col-xl-5">
