              alt=""
            />
            <button class="btn btn-outline-primary">Подать заявку</button>
          </div>
        </div>

        <hr />

        <b-card no-body>
          <b-tabs pills card vertical nav-wrapper-class="col-12 col-md-4">
            <b-tab title="Описание" active>
              <b-card-text>
                <h3>Описание</h3>
                <p>
                  Гранты предоставляются в целях финансового обеспечения
                  (возмещения) затрат в связи с выполнением работ, направленных
                  на создание научных (научно-исследовательских) и (или)
                  научно-технических проектов, а также проектов по разработке и
                  (или) внедрению инновационных продуктов, технологий в
                  городское хозяйство и (или) социальную сферу города (далее —
                  научный проект), а именно затрат на:
                </p>
                <ul>
                  <li>
                    приобретение специального оборудования (в том числе
                    электронно-вычислительной техники), расходных материалов,
                    комплектующих, необходимых для проведения научного проекта;
                  </li>
                  <li>
                    оплату выполнения работ и оказания услуг производственного
                    характера, выполняемых сторонними организациями,
                    индивидуальными предпринимателями, физическими лицами по
                    договорам гражданско-правового характера, связанных с
                    выполнением научного проекта;
                  </li>
                  <li>
                    оплату выполнения научных и (или) научно-технических работ
                    соисполнителями — сторонними организациями, индивидуальными
                    предпринимателями, физическими лицами по договорам
                    гражданско-правового характера, связанных с выполнением
                    научного проекта;
                  </li>
                  <li>
                    приобретение готового или разработку специального
                    программного обеспечения, необходимого для выполнения
                    научного проекта;
                  </li>
                  <li>
                    участие в выездных мероприятиях по теме научного проекта;
                  </li>
                  <li>публикации по теме научного проекта;</li>
                  <li>
                    оплату государственной пошлины на регистрацию
                    интеллектуальной собственности по теме научного проекта;
                  </li>
                  <li>
                    вознаграждение соискателя гранта за выполнение научного
                    проекта.
                  </li>
                </ul>
                <p>
                  Результатом предоставления гранта является выполнение научного
                  проекта.
                </p>
              </b-card-text>
            </b-tab>
            <b-tab title="Требования к заявителю">
              <b-card-text
                ><h3>Требования к заявителю</h3>
                <p>
                  Получателями грантов являются физические лица, занимающиеся
                  научной (научно-исследовательской), научно-технической или
                  инновационной деятельностью научных организациях,
                  образовательных организациях высшего образования, иных
                  организациях, один из видов осуществляемой деятельности
                  которых относится к классу 72 «Научные исследования и
                  разработки» раздела M «Деятельность профессиональная, научная
                  и техническая» Общероссийского классификатора видов
                  экономической деятельности ОК 029-2014 (КДЕС Ред. 2),
                  принятого приказом Федерального агентства по техническому
                  регулированию и метрологии от 31.01.2014 № 14-ст, за
                  исключением государственных (муниципальных) учреждений (далее
                  — организации, осуществляющие научные исследования и
                  разработки), отвечающие требованиям, предусмотренным пунктом
                  2.4 Порядка, и являющиеся на дату подачи заявки на
                  предоставление гранта (далее — заявка):
                </p>
                <ul>
                  <li>
                    студентами (курсантами) — лицами, осваивающими
                    образовательные программы бакалавриата, специалитета,
                    магистратуры в возрасте до 30 лет включительно;
                  </li>
                  <li>
                    аспирантами, научными работниками, педагогическими
                    работниками, специалистами (инженерно-техническими
                    работниками) образовательных организаций высшего образования
                    или научных организаций без ученой степени в возрасте до 30
                    лет включительно;
                  </li>
                  <li>
                    научными работниками, педагогическими работниками, имеющими
                    ученую степень кандидата наук в возрасте до 35 лет
                    включительно либо имеющими ученую степень доктора наук в
                    возрасте до 40 лет включительно;
                  </li>
                  <li>
                    специалистами либо руководителями организаций,
                    осуществляющих научные исследования и разработки, в возрасте
                    до 35 лет включительно.
                  </li>
                </ul>
                <p>
                  Получатели грантов должны соответствовать на первое число
                  месяца, в котором объявлен конкурс на предоставление гранта,
                  следующим требованиям:
                </p>
                <ul>
                  <li>
                    не должны являться получателями средств из бюджета города на
                    цели предоставления гранта, указанные в пункте 1.3 Порядка,
                    в течение двух предшествующих лет;
                  </li>
                  <li>
                    не должны иметь просроченную задолженность по возврату в
                    бюджет города субсидий, бюджетных инвестиций, и иную
                    просроченную задолженность перед бюджетом города;
                  </li>
                  <li>
                    не должны иметь неисполненную обязанность по уплате налогов,
                    сборов, страховых взносов, пеней, штрафов и процентов,
                    подлежащих уплате в соответствии с законодательством
                    Российской Федерации о налогах и сборах.
                  </li>
                </ul>
              </b-card-text>
            </b-tab>
            <b-tab
              title="Необходимые документы для предоставления меры поддержки"
            >
              <b-card-text>
                <h3>Необходимые документы для предоставления меры поддержки</h3>
                <p>
                  Для участия в конкурсе соискатель гранта вносит данные о себе
                  в предусмотренные для заполнения поля в Системе и размещает в
                  ней электронные образы следующей конкурсной документации,
                  полученной в результате сканирования документов на бумажных
                  носителях:
                </p>
                <ul>
                  <li>
                    заявки по форме согласно приложению 1 к Порядку,
                    согласованной руководителем организации, в которой учится
                    или работает соискатель гранта, и печатью данной организации
                    (при наличии);
                  </li>
                  <li>
                    нотариально заверенного перевода документа, удостоверяющего
                    личность соискателя гранта (в случае если соискатель гранта
                    является иностранным гражданином);
                  </li>
                  <li>
                    ходатайства ученого совета научной или образовательной
                    организации высшего образования, руководителя организации,
                    осуществляющей научные исследования или разработки, в
                    которой учится или работает соискатель гранта, о
                    предоставлении гранта соискателю гранта, включающего в себя
                    описание значимости результатов, полученных в рамках
                    научного проекта, личного вклада соискателя гранта в
                    достижение полученных результатов и согласие на
                    предоставление соискателю гранта условий для выполнения
                    научного проекта по теме гранта;
                  </li>
                  <li>
                    диплома о присуждении соискателю гранта ученой степени либо
                    приказа министерства науки и высшего образования Российской
                    Федерации о выдаче диплома (при наличии);
                  </li>
                  <li>
                    финансовых документов, подтверждающих осуществление затрат,
                    предусмотренных пунктом 1.3 Порядка (в случае подачи заявки
                    в целях возмещения затрат, понесенных в течение 12 месяцев,
                    предшествующих дате подачи заявки);
                  </li>
                  <li>
                    свидетельства о постановке соискателя гранта на учет в
                    налоговом органе;
                  </li>
                  <li>
                    документа, подтверждающего регистрацию в системе
                    индивидуального (персонифицированного) учета, в том числе в
                    форме электронного документа;
                  </li>
                  <li>
                    согласия соискателя гранта на публикацию (размещение) в
                    информационно-телекоммуникационной сети «Интернет»
                    информации о соискателе гранта, информации, содержащейся в
                    заявке соискателя гранта и иной информации, связанной с
                    участием в конкурсе;
                  </li>
                  <li>
                    согласия соискателя гранта и лиц, являющихся поставщиками
                    (подрядчиками, исполнителями) по договорам, заключенным в
                    целях исполнения обязательств по договорам о предоставлении
                    грантов на финансовое обеспечение затрат в связи с
                    производством (реализацией) товаров, выполнением работ,
                    оказанием услуг (за исключением государственных
                    (муниципальных) унитарных предприятий, хозяйственных
                    товариществ и обществ с участием публично-правовых
                    образований в их уставных (складочных) капиталах, а также
                    коммерческих организаций с участием таких товариществ и
                    обществ в их уставных (складочных) капиталах), на
                    осуществление департаментом и органами муниципального
                    финансового контроля проверок соблюдения условий, целей и
                    порядка предоставления гранта;
                  </li>
                  <li>
                    согласия соискателя гранта на обработку департаментом его
                    персональных данных в соответствии с Федеральным законом от
                    27.07.2006 № 152-ФЗ «О персональных данных».
                  </li>
                </ul>
              </b-card-text>
            </b-tab>
            <b-tab title="Порядок рассмотрения заявок участников">
              <b-card-text>
                <h3>Порядок рассмотрения заявок участников</h3>
                <p>
                  Департамент в течение срока приема заявок осуществляет
                  предварительный анализ заявок соискателей и документов на
                  предмет правильности и полноты их представления и, в случае
                  необходимости уточнения информации, осуществляет подготовку
                  письменного уведомления, направляет его соискателю гранта
                  посредством Системы на адрес электронной почты, указанный в
                  заявке.
                </p>
                <p>
                  Предварительный анализ осуществляется только в случае подачи
                  соискателем гранта заявки не позднее чем за 10 дней до даты
                  окончания срока приема заявок (включительно).
                </p>
                <p>
                  Соискатель гранта вправе представить скорректированную
                  (уточненную) заявку до даты окончания срока приема заявок
                  (включительно). Непредставление соискателем гранта
                  скорректированной (уточненной) заявки в указанный срок
                  расценивается как отказ от участия в конкурсе.
                </p>
                <p>
                  Департамент в течение 15 дней со дня окончания срока приема
                  заявок принимает решение о признании соискателя гранта
                  участником конкурса при отсутствии оснований, предусмотренных
                  пунктом 2.9 Порядка, направляет письма о назначении экспертов.
                </p>
                <p>
                  Экспертиза поданных на участие в конкурсе заявок и подготовка
                  экспертного заключения осуществляется экспертами в Системе в
                  течение 14 дней со дня назначения им заявок.
                </p>
                <p>
                  Оценка заявок участников конкурса экспертами осуществляется по
                  шкале от 0 до 10 баллов с последующим суммированием баллов по
                  критериям. Итоговое количество баллов определяется как средняя
                  арифметическая сумма баллов, присвоенных каждым экспертом по
                  всем критериям оценки (с округлением до тысячных).
                </p>
                <p>
                  Заявка участника конкурса не допускается к рассмотрению на
                  заседании координационного совета при наличии хотя бы одного
                  заключения о несоответствии научного проекта.
                </p>
                <p>
                  Департамент в течение пяти дней со дня завершения оценки
                  экспертами последней заявки формирует общий рейтинг участников
                  конкурса.
                </p>
                <p>
                  Координационный совет не позднее 20 дней со дня получения
                  доступа к просмотру заявок участников конкурса, экспертным
                  заключениям и общему рейтингу участников рассматривает заявки
                  и принимает решение о признании участников конкурса
                  победителями с указанием размера грантов.
                </p>
                <p>
                  Победителями конкурса признаются участники, набравшие
                  наибольшее количество баллов и имеющие наиболее высокие
                  позиции в общем рейтинге. Победителем конкурса не может быть
                  признан участник, заявка которого набрала менее 3 баллов.
                </p>
                <p>
                  Координационный совет имеет право принять решение, отличное от
                  мнения экспертов в части количества баллов, набранных
                  участником конкурса, с последующим изменением общего рейтинга,
                  обосновав свое решение в соответствии с критериями оценки, и
                  занесением соответствующей информации в протокол заседания.
                </p>
                <p>
                  Протокол заседания координационного совета, содержащий
                  информацию об общем рейтинге участников и победителях
                  конкурса, подписывается председателем и секретарем
                  координационного совета в течение 10 дней с даты проведения
                  заседания координационного совета и направляется в
                  департамент.
                </p>
              </b-card-text>
            </b-tab>
            <b-tab title="Администратор меры">
              <b-card-text>
                <h3>Администратор меры</h3>
                <p>Территориальный уровень меры поддержки: Региональный</p>
                <p>
                  Название организации: Департамент промышленности, инноваций и
                  предпринимательства
                </p>
                <p>
                  Ссылка на сайт администратора меры поддержки:
                  <a href="https://dep.test.ru">https://dep.test.ru/</a>
                </p>
                <p>
                  Контактная информация: 200-00-00, e-mail:
                  <a href="mailto:test@test.ru">test@test.ru</a>
                </p>
              </b-card-text>
            </b-tab>
            <b-tab title="Нормативная правовая база">
              <b-card-text>
                <h3>Нормативно-правовая база</h3>
                <p>
                  Конкурс проводится в соответствии с Порядком предоставления
                  грантов в форме субсидий в сфере научной и инновационной
                  деятельности, утвержденным постановлением от 01.01.2021 № 200.
                </p>
              </b-card-text>
            </b-tab>
          </b-tabs>
        </b-card>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: "SubsidyInfo",
};
</script>

<style lang="scss">
