          .org-logo {
            height: 38px;
            margin-right: $gap/2;
            margin-top: $gap/2;
          }
        }
      }

      .support__conditions {
        @media (min-width: 768px) {
          text-align: right;
        }

        .support__conditions__header {
          color: $support-color;
        }

        .support__size {
          font-size: 1.75rem;
          font-weight: 500;
        }

        .support__size__info {
          color: $support-color;
        }
      }

      .support__buttons {
        display: flex;
        align-items: start;

        @media (min-width: 768px) {
          justify-content: flex-end;
        }

        .support__icon {
          height: 38px;
          border: 1px solid $basic-color;
          border-radius: 0.25rem;
          margin-right: $gap/2;
        }
      }
    }

    hr {
      margin-top: 0;
    }

    .card {
      border: none;

      .nav {
        background-color: transparent;
        padding: 0;
        margin-bottom: $gap;

        @media (min-width: 768px) {
          padding-right: 1.25rem;
        }
      }

      .tab-content {
        margin-bottom: $gap;

        .tab-pane {
          padding: 0;

          @media (min-width: 768px) {
            padding-left: 1.25rem;
          }
        }

        ul {
          padding-left: 1.25rem;
        }

